import React from 'react';
import styled from 'styled-components';

const ThumbItemContainer = styled.div`
    width: 150px;
    height: 150px;
    border: 1px solid tomato;
    position: relative;
    img{
        width: 100%;
        object-fit: fill;
    }
    .heart{
        position: absolute;
        right: 10px;
        bottom: 10px;
    }
`;

const ThumbItem = ({item}) => {
    return (
        <ThumbItemContainer>
            <img src="" alt="thumbnail" />
            <div className="heart">
                
            </div>
        </ThumbItemContainer>
    );
};

export default ThumbItem;