import { createGlobalStyle } from 'styled-components';

/** reset*/
import reset from "styled-reset";
// import './assets/css/reset.css';

const GlobalStyles = createGlobalStyle`
    ${ reset };
    *{
        margin: 0;
        padding: 0;
    }

    html { 
        font-family: SUIT, sans-serif;
        font-size: 12px;
    }
    a { text-decoration: none; }
    button { border: 0; }
    
    :root {
        // Point, Button, Icon
        --blue : #0058FF;

        // Icon 
        --subblue: #7E84A3;
        
        // Tab Icon
        --gray: #9E9E9D;
        
        // Tab Color
        --sky : #E5EEFF;
        
        // Tag, Line 
        --subgray : #E0E0E0;

        // Back Wall
        --subsky : #F4F7FC;
        
        //  Text
        --textgray : #595959;

        //  Text
        --black : #000;

        // White Text
        --white : #fff;

        // Notification
        --red : #F0142F;
    }

    // 36px        
    .font1 {
        font-weight: 200;
        font-size: 3rem;
    }

    // 24px  ,  ,   
    .font2 {
        font-weight: 800;
        font-size: 2rem;
    }

    // 18px
    .font3 {
        font-weight: 800;
        font-size: 1.5rem;
    }

    // 18px
    .font4 {
        font-weight: 400;
        font-size: 1.5rem;
    }

    // 18px  
    .font4 {
        font-weight: 400;
        font-size: 1.5rem;
        line-height: 0.75cm;
    }

    // 16px
    .font5 {
        font-weight: 600;
        font-size: 1.3rem;
    }

    // 16px
    .font6 {
        font-weight: 400;
        font-size: 1.3rem;
    }

    // 16px  ,  
    .font7 {
        font-weight: 400;
        font-size: 1.3rem;
        line-height: 0.7cm;
    }

    // 14px
    .font8 {
        font-weight: 600;
        font-size: 1.15rem;
    }

    // 14px
    .font9 {
        font-weight: 400;
        font-size: 1.15rem;
    }
`;

export default GlobalStyles;
