import React, { memo } from "react";
import styled from "styled-components";
import { NavLink } from "react-router-dom";

import Theme1 from "../../assets/img/img001.jpeg";
import Pic1 from "../../assets/img/img005.jpeg";
import Pic2 from "../../assets/img/img003.jpeg";
import Pic3 from "../../assets/img/img004.jpeg";

const ThemeDetailContainer = styled.div`
    width: 100%;

    .themedetail_imgwrapper {
        position: relative;
        width: 100%;
        height: 30vh;

        img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            transform: translate(50, 50);
            object-fit: cover;
            margin: auto;
        }
    }

    .themedetail_title {
        width: 80%;
        height: 18vh;
        margin: 0 auto;
        position: relative;
        padding-top: 6%;
        padding-bottom: 20%;

        p,
        h1 {
            color: var(--white);
        }

        p {
            padding-top: 4%;
            padding-bottom: 4%;
        }
    }

    .themedetail_content {
        width: 80%;
        margin: 0 auto;
        padding-top: 6%;
        padding-bottom: 20%;

        p {
            padding-top: 4%;
            padding-bottom: 4%;
            color: var(--black);
        }

        h1 {
            padding-bottom: 4%;
            color: var(--black);
        }

        img {
            width: 100%;
            height: auto;
            margin: 0 auto;
            position: relative;
            padding: 4% 0 4% 0;
        }

        .todetail {
                width: 100%;
                height: 2.4em;
                border: none;
                border-radius: 1mm;
                color: var(--white);
                background-color: var(--blue);
            }
    }
`;

const ThemeDetail = memo(() => {
    return (
        <ThemeDetailContainer>
            <div className="themedetail_imgwrapper">
                <img src={Theme1} alt="theme1" />
                <div className="themedetail_title">
                    <p className="font3">   </p>
                    <h1 className="font1"></h1>
                </div>
            </div>

            <div className="themedetail_content">
                <p className="font4">
                         .           . 3   1         .
                </p>
                <br /><hr /><br /><br />
                <p className="font3">2022 </p>
                <h1 className="font1"></h1>
                <p className="font4"> 3 . 1  6~7 2 30 , 2  6,3km 2, 3  3 1 20 .        1 2022   .      1  .</p>
                <img src={Pic1} alt='pic1' />
                <p className="font4">  , , , , ,   ,  .            .</p>
                <img src={Pic2} alt='pic2' />
                <img src={Pic3} alt='pic3' />
                <p className="font4">
                1      ,              .                 .      1  .            .       .
                </p>
                <br />
                <NavLink to="/tab/:api/:id">
                    <button type="submit" id="signup" className="todetail font4">
                         
                    </button>
                </NavLink>
                {/* <p className="font3"></p>
                <p className="font4">   2022-05-24  ,          .
                <br />
                <br />
                    , ,           .</p> */}
            </div>

            
        </ThemeDetailContainer>
    );
});

export default ThemeDetail;