import React, { useEffect } from 'react';
import { useParams, NavLink } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import { getNoticeList } from '../slices/NoticeSlice';
import { getFAQList } from '../slices/FAQSlice';
import styled from 'styled-components';

import BoardItem from '../components/items/BoardItem';

const BoardPageContainer = styled.div`
    width: 100%;

    .content {
        width: 80%;
        margin: 0 auto;

        h1 {
            padding: 20%;
            text-align: center;
            border-bottom: 1px solid var(--subblue);
        }

        button {
            margin-top: 50px;
            padding: 15px;
            width: 100%;
        }
    }
`;

const BoardPage = () => {
    // path   
    const { api } = useParams();

    // redux  
    const dispatch = useDispatch();
    const { data, loading, error } = useSelector((state) => state[api]);

    // api    hook
    useEffect(() => {
        api === 'notice' ? dispatch(getNoticeList()) : dispatch(getFAQList());
    }, [dispatch, api]);

    return (
        <BoardPageContainer>
            {api === 'notice' ? (
                <div className='content'>
                    <h1></h1>
                    {data && data.map((v, i) => <BoardItem key={i} item={v} api={api}/>)}
                </div>
            ) : (
                <div className='content'>
                    <h1>  </h1>
                    {data && data.map((v, i) => <BoardItem key={i} item={v} api={api}/>)}
                    <NavLink to='/qna'>
                        <button type='button'>1:1 </button>
                    </NavLink>
                </div>
            )}
        </BoardPageContainer>
    );
};

export default BoardPage;