import React from "react";
import styled from "styled-components";
import { NavLink } from "react-router-dom";

import Arrow from "../assets/icon/arrow.png";

const SignupContainer = styled.div`
    width: 100%;
    padding-top: 20%;
    padding-bottom: 20%;

    h3 {
        padding-bottom: 12%;
        display: flex;
        justify-content: center;
    }

    .signup_content {
        width: 80%;
        display: flex;
        margin: 0 auto;
        flex-direction: column;
        flex-wrap: wrap;
        align-content: center;

        form {
            width: 100%;

            .message {
                display: flex;
                padding-bottom: 2%;
            }

            label {
                font-size: 14px;
            }

            p {
                font-size: 14px;
                color: red;
            }

            /* select    */
            select {
                appearance: none;
                -moz-appearance: none; /* Firefox */
                -webkit-appearance: none; /* Safari and Chrome */
                color: var(--gray)
            }

            /* select IE    */
            select::-ms-expand {
                display: none; /* IE 10, 11 */
            }

            .err_msg {
                color: red;
                display: flex;
                font-size: 14px;
                padding: 4% 0 4% 0;
            }

            .input_text {
                width: 90%;
                height: 16px;
                padding: 5%;
                border: solid 1px #eee;
                border-radius: 1mm;
            }

            .birth_area {
                width: 100%;
                display: flex;
                justify-content: space-between;

                .user_birth {
                    width: 20%;
                    height: 16px;
                    padding: 5%;
                    border: solid 1px #eee;
                    border-radius: 1mm;
                }

                .user_mm {
                    width: 30%;
                    height: auto;
                    padding: 4%;
                    border: solid 1px #eee;
                    border-radius: 1mm;
                }
            }

            .email_area {
                width: 100%;
                display: flex;
                justify-content: space-between;

                .user_email {
                    width: 40%;
                    height: 16px;
                    padding: 5%;
                    border: solid 1px #eee;
                    border-radius: 1mm;
                }

                .user_email_sel {
                    width: 45%;
                    height: auto;
                    padding: 5%;
                    border: solid 1px #eee;
                    border-radius: 1mm;
                }
            }

            .signup {
                font-size: 16px;
                width: 100%;
                height: 2.4em;
                border: none;
                border-radius: 1mm;
                color: var(--white);
                background-color: var(--blue);
            }
        }
    }
`;

const Signup = () => {
    /**      */
    const month = [];
    for (let i = 1; i < 13; i++) month.push(i);

    return (
        <SignupContainer>
            <div className="signup_content">
                <h3 className="headfont"></h3>

                <form>
                    {/*  */}
                    <div>
                    <div className="message">
                        <label htmlFor="user_id"></label>&nbsp;<p>*</p>
                    </div>
                    <input
                        id="user_id"
                        name="user_id"
                        type="text"
                        className="input_text"
                    ></input>
                    <span id="err_id" className="err_msg">
                         .
                    </span>
                    <br />
                    </div>

                    {/*  */}
                    <div>
                    <div className="message">
                        <label htmlFor="user_pw"></label>&nbsp;<p>*</p>
                    </div>
                    <input
                        id="user_pw"
                        name="user_pw"
                        type="password"
                        className="input_text"
                        placeholder="+  8~16"
                    ></input>
                    <span id="err_pw" className="err_msg">
                         .
                    </span>
                    <br />
                    </div>

                    {/*   */}
                    <div>
                    <div className="message">
                        <label htmlFor="user_pw_check"> </label>
                        &nbsp;<p>*</p>
                    </div>
                    <input
                        id="user_pw_check"
                        name="user_pw_check"
                        type="password"
                        className="input_text"
                    ></input>
                    <span id="err_pw_check" className="err_msg">
                          .
                    </span>
                    <br />
                    </div>

                    {/*  */}
                    <div>
                    <div className="message">
                        <label htmlFor="user_name"></label>&nbsp;<p>*</p>
                    </div>
                    <input
                        id="user_name"
                        name="user_name"
                        type="text"
                        className="input_text"
                    ></input>
                    <span id="err_name" className="err_msg">
                         .
                    </span>
                    <br />
                    </div>

                    {/*  */}
                    <div className="message">
                        <label htmlFor="user_yy"></label>
                    </div>

                    <div>
                    <div className="birth_area">
                        <input
                            id="user_yy"
                            name="user_yy"
                            type="text"
                            placeholder="(4)"
                            area-label="(4)"
                            className="user_birth"
                            maxLength={4}
                        />

                        <select 
                            id="user_mm"
                            name="user_mm"
                            className="user_mm"
                            area-label=""
                        >
                            <option value=""></option>
                            {month.map((v, i) => {
                                return (
                                    <option value={v} key={i}>
                                        {v}
                                    </option>
                                );
                            })}
                        </select>

                        <input
                            id="user_dd"
                            name="user_dd"
                            type="text"
                            placeholder=""
                            area-label=""
                            className="user_birth"
                            maxLength={2}
                        />
                    </div>

                    <span id="err_birth" className="err_msg">
                         .
                    </span>
                    <br />
                    </div>

                    {/*  */}
                    <div className="message">
                        <label htmlFor="user_email"></label>
                    </div>

                    <div className="email_area">
                        <input
                            id="user_email"
                            name="user_email"
                            type="text"
                            className="user_email"
                            maxLength={20}
                        />

                        <select
                            id="user_email"
                            name="user_email"
                            type="text"
                            className="user_email_sel"
                            placeholder="@gmail.com"
                        >
                            <option value="gmail">@gmail.com</option>
                            <option value="gmail">@naver.com</option>
                            <option value="gmail">@daum.com</option>
                        </select>
                    </div>

                    <span id="err_email" className="err_msg">
                          .
                    </span>
                    <br />

                    <NavLink to="/">
                        <button type="submit" id="signup" className="signup">
                            
                        </button>
                    </NavLink>
                </form>
            </div>
        </SignupContainer>
    );
};

export default Signup;
