import React, { useEffect, useCallback, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import styled,{keyframes} from 'styled-components';

const fadeIn = keyframes`
    from{
        transform: translateX(100%);
    }
    to{
        transform: translateX(0);
    }
`;

const SidebarContainer = styled.div`
    width: 100%;
    height: 97vh;
    position: absolute;
    top: 7vh;
    background-color: rgba(255,255,255,0);
    z-index: 80;
    animation: ${fadeIn} 0.3s;

    .sidebar_content{
        box-sizing: border-box;
        width: 70%;
        margin-left: 30%;
        height: 97vh;

        background-color: var(--white);
        padding: 20px 20px;
        .back{
            width: 100%;
            
            background-color: black;
        }
        .login{
            background-color: white;
            height: 15%;
            display: flex;
            justify-content: center;
            align-items: center;
        }
        .user_inform{
            background-color: white;
            height: 15%;
            display: flex;
            justify-content: center;
            align-items: center;
            .profile_img{
                background-color: tomato;
                width: 70px;
                height: 70px;
                border-radius: 50%;
                img{
                    
                }
            }
            .profile_text{
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center; 
            }
        }
        .menu{
            margin: 15px 0;
            li{
                display: block;
                width: 100%;
                padding: 15px 0;
            }//li end
        }//menu end
    }//sidebar_content end
`;

const Sidebar = ({setShowSidebar}) => {
    //  _  .
    const [isLogin, setIsLogin] = useState(true);
    
    //   sidebar 
    const navigate = useNavigate();
    const movePage=useCallback((e)=>{
        navigate(e.target.dataset.path);
        setShowSidebar(false);
    },[])
    //   sidebar  +
    const movePage2=useCallback((e)=>{
        if(isLogin===true){
            navigate(e.target.dataset.path);
            setShowSidebar(false);
        }else{
            alert("")
            navigate("/login");
            setShowSidebar(false);
        }
    },[])

    //sidebar unmount fadeout ??
    useEffect(()=>{
        return(()=>{

        })
    },[])

    return (
        <SidebarContainer>
            <div className="sidebar_content">
            <div className="back"></div>
            {
                //   
                isLogin? ( 
                    <div className="user_inform" data-path="/profile" onClick={movePage}>
                        <div className='profile_img'>
                            <img src="" alt="img" />
                        </div>
                        <div className="profile_text">
                            <h1>Hello</h1>
                            <h1>Bona!()</h1>
                        </div>
                    </div>
                    ):(
                        <div className='login' data-path='/login' onClick={movePage}>
                        /
                        </div>
                    )
            }
            {/* menu  */}
            <ul className='menu'>
            <li onClick={movePage2} data-path='/mylike'> </li>
            <li onClick={movePage2} data-path='/myreview'> </li>
            <li onClick={movePage2} data-path='/myqna'> </li>
            <li onClick={movePage} data-path='/tourkit'></li>
            <li onClick={movePage} data-path='/service'></li>
            {
                isLogin? (<li></li>):''
            }
            </ul>
            </div>
        </SidebarContainer>
        
    );
}; 

export default Sidebar;