import React, { useCallback, useEffect } from "react";
import { useParams } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import { getPlaceList } from "../slices/PlaceSlice";
import { getAccomList } from "../slices/AccomSlice";
import { getFoodList } from "../slices/FoodSlice";
import styled from "styled-components";

import TabArea from "../components/TabArea";
import ListItem from "../components/items/ListItem";
import HashtagBtn from "../components/buttons/HashtagBtn";

const TabPageContainer = styled.div`
    .content_wrap {
        width: 80%;
        margin: auto;
        margin-top: 20px;
        .hashtag_wrap {
            display: flex;
            flex-wrap: wrap;
        }
        .list_wrap {
        }
    }
`;

const TabPage = () => {
    //path  
    const { api } = useParams();
    let tagArr = ["", "", "", "", ""];
    if (api === "place") {
    } else if (api === "accom") {
    } else if (api === "food") {
    }

    //redux   
    const dispatch = useDispatch();
    const { data, loading, error } = useSelector((state) => state[api]);

    //tab  
    useEffect(() => {
        if (api === "place") {
            dispatch(getPlaceList());
        } else if (api === "accom") {
            dispatch(getAccomList());
        } else if (api === "food") {
            dispatch(getFoodList());
        }
    }, [dispatch, api]);

    // 
    const onTagClick = useCallback(() => {
        // css
        //     
    });

    return (
        <TabPageContainer>
            <TabArea/>
            <div className="content_wrap">
                <div className="hashtag_wrap">
                    {tagArr.map((v, i) => (
                        <HashtagBtn key={i} to="" onClick="">
                            {v}
                        </HashtagBtn>
                    ))}
                </div>
                <div className="list_wrap">
                    {data &&
                        data.map((v, i) => {
                            return <ListItem key={i} item={v} api={api}></ListItem>;
                        })}
                </div>
            </div>
        </TabPageContainer>
    );
};

export default TabPage;
