import BadRequestException from '../exceptions/BadRequestException';

class RegexHelper {
    // constructor() {}

    /** 
     *    .
     * @param {HTMLElement} field    <INPUT> DOM 
     * @param {string} msg            
     */
    value(field, msg) {
        // content  field value ;
        const content = field.value;
        
        //   `undefined` or `null` or ` string,    0 
        if (content === undefined || content === null || (typeof content === 'string' && content.trim().length === 0)) {
            //  BadRequestException (, <INPUT> DOM ) 
            throw new BadRequestException(msg, field);
        }

        return true;
    }

    /**
     *     .
     * @param {HTMLElement} field     <INPUT> DOM 
     * @param {int} len             
     * @param {string} msg             
     */
    minLength(field, len, msg) {
        this.value(field, msg);
        // content  field value ;
        const content = field.value;

        //      ?
        if (content.trim().length < len) {
            //  BadRequestException (, <INPUT> DOM ) 
            throw new BadRequestException(msg, field);
        }

        return true;
    }

    /**
     *     .
     * @param {HTMLElement} field     <INPUT> DOM 
     * @param {int} len             
     * @param {string} msg             
     */
    maxLength(field, len, msg) {
        this.value(field, msg);
        // content  field value ;
        const content = field.value;

        //      ?
        if (content.trim().length > len) {
            //  BadRequestException (, <INPUT> DOM ) 
            throw new BadRequestException(msg, field);
        }

        return true;
    }

    /**
     *    .
     * @param {string} origin      <INPUT> DOM 
     * @param {string} compare      <INPUT> DOM 
     * @param {string} msg           
     */
    compareTo(origin, compare, msg) {
        this.value(origin, msg);
        this.value(compare, msg);

        let src = origin.value.trim(); //  .
        let dsc = compare.value.trim(); //  .

        //    ?
        if (src !== dsc) {
            throw new BadRequestException(msg, origin);
        }

        return true; // 
    }

    /**
     *     .
     * @param {HTMLElement} field    <INPUT> DOM 
     * @param {string} msg            
     */
    check(field, msg) {
        const checkedItem = Array.from(field).filter((v, i) => v.checked);

        if (checkedItem.length === 0) {
            throw new BadRequestException(msg, field[0]);
        }
    }

    /**
     *      .
     * @param {HTMLElement} field    <INPUT> DOM 
     * @param {string} msg            
     */

    checkMin(field, len, msg) {
        const checkedItem = Array.from(field).filter((v, i) => v.checked);

        if (checkedItem.length < len) {
            throw new BadRequestException(msg, field[0]);
        }
    }

    /**
     *      .
     * @param {HTMLElement} field    <INPUT> DOM 
     * @param {string} msg            
     */

    checkMax(field, len, msg) {
        const checkedItem = Array.from(field).filter((v, i) => v.checked);

        if (checkedItem.length > len) {
            throw new BadRequestException(msg, field[0]);
        }
    }

    /**
     *    .
     * @param {HTMLElement} field     <INPUT> DOM 
     * @param {string} msg          
     * @param {object} regexExpr    
     */
    field(field, msg, regexExpr) {
        this.value(field, msg);

        const content = field.value.trim();

        //     ?
        if (!regexExpr.test(content)) {
            throw new BadRequestException(msg, field);
        }

        return true;
    }
    
    /**
     *      field()  .
     * @param {HTMLElement} field    <INPUT> DOM 
     * @param {string} msg         
     */
    num(field, msg) {
        return this.field(field, msg, /^[0-9]*$/);
    }

    /**
     *      field()  .
     * @param {HTMLElement} field    <INPUT> DOM 
     * @param {string} msg         
     */
    eng(field, msg) {
        return this.field(field, msg, /^[a-zA-Z]*$/);
    }

    /**
     *      field()  .
     * @param {HTMLElement} field    <INPUT> DOM 
     * @param {string} msg         
     */
    kor(field, msg) {
        return this.field(field, msg, /^[--]*$/);
    }

    /**
     *       field()  .
     * @param {HTMLElement} field    <INPUT> DOM 
     * @param {string} msg         
     */
    engNum(field, msg) {
        return this.field(field, msg, /^[a-zA-Z0-9]*$/);
    }

    /**
     *       field()  .
     * @param {HTMLElement} field    <INPUT> DOM 
     * @param {string} msg         
     */
    korNum(field, msg) {
        return this.field(field, msg, /^[--0-9]*$/);
    }
    
    /**
     *       field()  .
     * @param {HTMLElement} field    <INPUT> DOM 
     * @param {string} msg         
     */
    engKor(field, msg) {
        return this.field(field, msg, /^[a-zA-Z--]*$/);
    }

    /**
     *   4     field()  .
     * @param {HTMLElement} field     <INPUT> DOM 
     * @param {string} msg             
     */
    birthYear(field, msg) {           // 19nn | 20nn   0~9 {2}
        return this.field(field, msg, /^(19|20)[0-9]{2}$/);
    }

    /**
     *      field()  .
     * @param {HTMLElement} field    <INPUT> DOM 
     * @param {string} msg         
     */
    email(field, msg) {
        return this.field(field, msg, /^([\w-]+(?:\.[\w-]+)*)@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[az]{2})?)$/i);
    }

    /**
     *      field()  .
     * @param {HTMLElement} field    <INPUT> DOM 
     * @param {string} msg         
     */
    cellphone(field, msg) {
        return this.field(field, msg, /^01(?:0|1|[6-9])(?:\d{3}|\d{4})\d{4}$/);
    }

    /**
     *      field()  .
     * @param {HTMLElement} field    <INPUT> DOM 
     * @param {string} msg         
     */
    telephone(field, msg) {
        return this.field(field, msg, /^\d{2,3}\d{3,4}\d{4}$/);
    }

    /**
     *           .
     * @param {HTMLElement} field    <INPUT> DOM 
     * @param {string} msg             
     */
    phone(field, msg) {
        this.value(field, msg);

        const content = field.value.trim();
        let check1 = /^01(?:0|1|[6-9])(?:\d{3}|\d{4})\d{4}$/ //  
        let check2 = /^\d{2,3}\d{3,4}\d{4}$/ //  

        //           ?
        if (!check1.test(content) && !(check2.test(content))) {
            throw new BadRequestException(msg, field);
        }

        return true; //  
    }
}

export default new RegexHelper();