class BadRequestException extends Error {
    constructor(msg = '  .', field = null) {
        super(msg);
        //  
        this._statusCode = 400;
        this._field = field;
    }

    get statusCode() {
        return this._statusCode;
    }

    get field() {
        return this._field;
    }

    set field(params) {
        this._field = params;
    }
}

export default BadRequestException;