import React from "react";
import styled from "styled-components";
import ReivewItem from "../../components/items/ReivewItem";

const MyReviewContainer = styled.div`
    width: 80%;
    margin: auto;
`;

const MyReview = () => {
    return (
        <MyReviewContainer>
            <h1> </h1>
            <ul>
                <ReivewItem></ReivewItem>
                <ReivewItem></ReivewItem>
                <ReivewItem></ReivewItem>
            </ul>
        </MyReviewContainer>
    );
};

export default MyReview;
