import React, { useEffect } from 'react';
import { NavLink } from 'react-router-dom';
import styled from 'styled-components';

const ServiceContainer = styled.div`
    width: 100%;

    .content {
        width: 80%;
        margin: 0 auto;

        h1 {
            padding: 20%;
            text-align: center;
        }

        .notice {
            box-sizing: border-box;
            width: 100%;
            padding: 20px 10px;
            background: var(--sky);
        }
        
        .faq {
            margin-top: 50px;

            .faq_title {
                display: flex;
                justify-content: space-between;
                margin-bottom: 30px;
            }
        }
        
        ul {            
            li {
                padding: 15px 0;
                border-bottom: 1px solid var(--subblue);
            }
        }

    }
`;

const ServiceCenter = () => { 
    return (
        <ServiceContainer>
            <div className='content'>
                <h1> </h1>
                <div className='notice'>
                    <NavLink to='/service/notice'>
                        <span></span>    
                    </NavLink>
                </div>
                <div className='faq'>
                    <div className='faq_title'>
                        <h3>  </h3>
                        <NavLink to='/service/faq'> &gt;</NavLink>
                    </div>
                    <ul>
                        {/* 1 = faq_no, faq (PK) */}
                        {/*     !      */}
                        <NavLink to='/service/faq/1'>
                            <li>Q.  .</li>
                        </NavLink>
                    </ul>
                </div>
            </div>
        </ServiceContainer>
    );
};

export default ServiceCenter;